<template>
            <div class="centerx example-loading" v-if="LOADING">
                <div class="fill-row-loading">
                    <div id="loading-point" class="vs-con-loading__container loading-example"> </div>
                </div>
            </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
    computed: {
        ...mapState({
            LOADING: state => state.menu.loading,
        }),
    },
    mounted() {
        this.$vs.loading({
            container: "#loading-point",
            type:"point",
        });
    },
}
</script>
